import React, { useEffect, useState } from 'react';

const Description = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const messages = [
    "Welcome to our website!",
    "Explore our services.",
    "Join our community.",
    "Contact us for more info.",
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % messages.length);
    }, 3000); // Adjust the slide duration (milliseconds per slide)

    return () => clearInterval(interval);
  }, []);

  const backgroundImages = [
    'https://images.unsplash.com/photo-1514975614034-99de12b7c550?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80',
    'https://images.unsplash.com/photo-1541427468627-a89a96e5ca1d?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80',
    'https://images.unsplash.com/photo-1510784722466-f2aa9c52fff6?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80',
    'https://images.unsplash.com/photo-1531297484001-80022131f5a1?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1720&q=80'
  ];

  return (
    <div className='relative h-64 md:h-96 lg:h-128 overflow-hidden'>
      <div className='carousel'>
        {messages.map((message, index) => (
          <div
            key={index}
            className={`carousel-item absolute inset-0 transform transition-transform ${
              currentIndex === index ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0'
            }`}
          >
            <div
              className='absolute inset-0 bg-cover bg-center'
              style={{ backgroundImage: `url(${backgroundImages[index]})` }}
            >
              <div className='absolute inset-0 bg-black opacity-75'></div>
              <div className='absolute inset-0 flex justify-center items-center'>
                <h1 className='text-white text-4xl md:text-6xl'>{message}</h1>
              </div>
            </div>
            <div className='absolute bottom-0 left-1/2 transform -translate-x-1/2 w-12 h-4 bg-white border-t-4 border-white rounded-t'></div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Description;
